import { Field, InputType } from "@nestjs/graphql";
import { Type } from "class-transformer";
import { IsNotEmpty, IsNumber, Max, Min, ValidateNested } from "class-validator";
import { PaypalBillingCycleFrequency } from "./paypal-billing-cycle-frequency";
import { PaypalBillingCyclePrice } from "./paypal-billing-cycle-price";


@InputType()
export class PaypalBillingCycles {

  @Field(() => PaypalBillingCycleFrequency)
  @Type(() => PaypalBillingCycleFrequency)
  @ValidateNested()
  frequency: PaypalBillingCycleFrequency;

  @Field(() => Number)
  @IsNotEmpty()
  @IsNumber()
  @Min(0)
  @Max(999)
  totalCycles: number;

  @Field(() => PaypalBillingCyclePrice)
  @Type(() => PaypalBillingCyclePrice)
  @ValidateNested()
  price: PaypalBillingCyclePrice;
  
}
